body {
  color: #fff;
  background-color: #000106;
  line-height: 1.5;
}

button {
  --glow-color: #d9b0ff;
  --glow-spread-color: rgba(191, 123, 255, .78);
  --enhanced-glow-color: #e7ceff;
  --btn-color: #643d88;
  border: 2px solid var(--glow-color);
  color: var(--glow-color);
  background-color: var(--btn-color);
  box-shadow: 0 0 1em .25em var(--glow-color), 0 0 4em 1em var(--glow-spread-color), inset 0 0 .75em .25em var(--glow-color);
  text-shadow: 0 0 .5em var(--glow-color);
  border-radius: 1em;
  outline: none;
  margin-left: 30px;
  padding: 1em 3em;
  font-size: 15px;
  font-weight: bold;
  transition: all .3s;
  position: relative;
}

button:after {
  pointer-events: none;
  content: "";
  height: 100%;
  width: 100%;
  background-color: var(--glow-spread-color);
  filter: blur(2em);
  opacity: .7;
  position: absolute;
  top: 120%;
  left: 0;
  transform: perspective(1.5em)rotateX(35deg)scale(1, .6);
}

button:hover {
  color: var(--btn-color);
  background-color: var(--glow-color);
  box-shadow: 0 0 1em .25em var(--glow-color), 0 0 4em 2em var(--glow-spread-color), inset 0 0 .75em .25em var(--glow-color);
}

button:active {
  box-shadow: 0 0 .6em .25em var(--glow-color), 0 0 2.5em 2em var(--glow-spread-color), inset 0 0 .5em .25em var(--glow-color);
}

button:disabled {
  background-color: #696969;
  box-shadow: 0 0 .6em .25em red;
}

